import { styled } from 'styled-components';

import Button from '#components/Button';
import { BUTTON_VARIANT_TERTIARY } from '#components/Button/Button.constants';
import StepCard from '#components/StepCard';

import * as device from '#theme/devices';

import { CLASS_IS_ACTIVE } from './MultiStepCard.constants';

export const TagLine = styled.span.attrs({ 'aria-current': 'step' })`
  display: block;
  margin-bottom: var(--spacing);
  color: ${({ theme }) => theme.color.ink.muted};
  font: bold 14px / var(--card-step-lh)
    ${({ theme }) => theme.fontFamily.secondaryFont};
  letter-spacing: 2px;
  text-transform: uppercase;

  .${CLASS_IS_ACTIVE} & {
    color: ${({ theme }) => theme.color.ink.base};
  }

  @media ${device.tablet} {
    font-size: 16px;
    line-height: 20px;
  }

  @media ${device.laptop} {
    font-size: 18px;
    line-height: 22px;
  }
`;

export const Title = styled.h2`
  margin: 0 0 var(--spacing);
  font: bold 20px/24px ${({ theme }) => theme.fontFamily.secondaryFont};

  @media ${device.tablet} {
    font-size: 22px;
    line-height: 26px;
  }
`;

export const Wrapper = styled(StepCard)`
  --active-border-color: ${({ theme }) => theme.color.primary.base};
  --active-border: 2px solid var(--active-border-color);
  --box-shadow: 0px -4px 18px 0px ${({ theme }) => theme.color.shadow.dark};
  position: relative;
  pointer-events: none;
  transition: max-height
    var(
      --card-transition,
      ${({ theme }) => theme.transition.onboardingJourneyWrapper}
    );

  ${Title},
  ${TagLine} {
    opacity: 0;
  }

  &.${CLASS_IS_ACTIVE} {
    border-top: var(--active-border);
    box-shadow: var(--box-shadow);
    pointer-events: all;

    ${Title},
    ${TagLine} {
      opacity: 1;
    }

    @media ${device.tablet} {
      border-right: var(--active-border);
      border-left: var(--active-border);
    }
  }
`;

export const InnerWrapper = styled.div`
  padding: ${({ theme }) => theme.spacing.x2}px
    ${({ theme }) => theme.spacing.x1_5}px ${({ theme }) => theme.spacing.x3}px;

  @media ${device.mobile} {
    padding-right: ${({ theme }) => theme.spacing.x2}px;
    padding-left: ${({ theme }) => theme.spacing.x2}px;
  }
`;

export const StepAndTitleWrapper = styled.div`
  --spacing: ${({ theme }) => theme.spacing.x1}px;
  margin-bottom: ${({ theme }) => theme.spacing.x3 + theme.spacing.x0_5}px;
  text-align: center;
`;

export const Description = styled.p`
  margin: 0;
  font: normal 16px/20px ${({ theme }) => theme.fontFamily.secondaryFont};
`;

export const GoBackToPreviousStep = styled(Button).attrs({
  variant: BUTTON_VARIANT_TERTIARY,
})`
  --top-padding: ${({ theme }) => theme.spacing.x1_5}px;
  display: block;
  position: absolute;
  top: calc(
    var(--card-top-padding, ${({ theme }) => theme.spacing.x2}px) - var(
        --top-padding
      )
  );
  left: 50%;
  color: ${({ theme }) => theme.color.primary.base};
  pointer-events: all;
  text-transform: capitalize;
  transform: translateX(-50%);
  white-space: nowrap;

  &:hover {
    color: ${({ theme }) => theme.color.actionBrand.alt};
  }

  .${CLASS_IS_ACTIVE} & {
    display: none;
  }

  @media ${device.tablet} {
    margin-top: var(--additional-inactive-card-padding-y);
  }
`;
