import classnames from 'classnames';
import { forwardRef } from 'react';

import { CLASS_IS_ACTIVE } from './MultiStepCard.constants';
import {
  Description,
  InnerWrapper,
  TagLine,
  StepAndTitleWrapper,
  Title,
  Wrapper,
} from './MultiStepCard.styles';
import MultiStepCardProps from './MultiStepCard.types';

const MultiStepCard = forwardRef<HTMLDivElement, MultiStepCardProps>(
  function MultiStepCard(
    {
      goBackToPreviousStep,
      currentStep,
      title,
      description,
      children,
      className,
      isCardActive,
      ...rest
    },
    ref,
  ) {
    return (
      <Wrapper
        className={classnames(className, {
          [CLASS_IS_ACTIVE]: isCardActive,
        })}
        ref={ref}
        {...rest}
      >
        <InnerWrapper>
          {goBackToPreviousStep}
          <StepAndTitleWrapper>
            {currentStep && <TagLine>{currentStep}</TagLine>}
            <Title dangerouslySetInnerHTML={{ __html: title }} />
            {description && <Description>{description}</Description>}
          </StepAndTitleWrapper>
          {children}
        </InnerWrapper>
      </Wrapper>
    );
  },
);

export default MultiStepCard;
